import actionType from '../../constant/operator'

const INITIAL_STATE ={
    isloading : true ,
    data : [],
    operator : [],
    message : null 
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.OPERATOR_LIST:
        return {
            isLoading : true,
        
        }

        case actionType.OPERATOR_LIST_SUCCESS :
            return {
               // ...state ,
                isloading : false ,
                create : false ,
                data : action.data[0]
            }

        case actionType.OPERATOR_CREATE_SUCCESS : 
            return {
               // ...state ,
               message : "done" 
            }
        case actionType.OPERATOR_DELETE : 
            return {
               // ...state ,
               message : " " 
            }
        case actionType.OPERATOR_DELETE_SUCCESS : 
            return {
               // ...state ,
               message : "done" 
            }
            case actionType.OPERATOR_UPDATE : 
            return {
               // ...state ,
               message : " " 
            }
        case actionType.OPERATOR_UPDATE_SUCCESS : 
            return {
               // ...state ,
               message : "done" 
            }

        
        default:
            return state;  
    }

}