import actionType from '../../constant/layout'

const INITIAL_STATE ={
    isloading : true ,
    data : [],
    current : null,
    menu : localStorage.getItem('login') ?  'Home' : 'Login',
    back : false ,

}


export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.LAYOUT_GET_MENU_DATA:
        return {
            ...state ,
            isLoading : true,
        
        }

        case actionType.LAYOUT_GET_MENU_DATA_SUCCESS :
            return {
                 ...state ,
                isloading : false ,
                data : action.data.HIERARCHIES.HIERARCHY
            }

        case actionType.LAYOUT_CURRENT_INDEX : 
            return {
                current : action.payload 
            }
        case actionType.LAYOUT_MENU : {
            return {
                ...state ,
                menu : action.payload,
                back : action.payload === 'Home' || action.payload === 'Registration' || action.payload === 'Setting' ? false : true 
            }
        }
        
        default:
            return state;  
    }

}