import Network from '../network/Network';
import axios from 'axios';

const header2 = 'Bearer  ' +  localStorage.getItem('header-z-my-point-new-look-my-base')

  export default   function getMethod(end,header) {
            
          let options = {
            tail : end[1],
            headers : {
              'Authorization' :  header
            } 
            
      }
        console.log(Network.network +'/' + end[0] ,options)
        return  axios.post(Network.network +'/' + end[0] ,options)
  }
  
  export  function getMethodById(end,data) {
        let options = {
          data : data,
          tail : end[1],
          // headers : {
          //   'Authorization' :  header
          // } 
          
    }
   // console.log(Network.network +'/' + end[0] ,options)
     return  axios.post(Network.network +'/' + end[0] ,options)
  }

  export  function postMethod(end,data,header) {
        
     // return  axios.post(Network.network +'/' + end , data)
     let options ={
      data : data,
      tail : end[1] ,
      headers : {
        'Content-Type': 'application/json',
        'Authorization' :  header
      } 
    }
    // console.log(Network.network +'/' + end[0] ,options)
    return  axios.post(Network.network +'/' + end[0] ,options )
    }

    export  function deleteMethod(end,data,header) {
      const url = Network.network +'/' + end[0] 

     let options = {
       data : data,
       tail : end[1],
       headers : {
        'Authorization' :  header
      } 
     }
      return axios.delete(url, { data: options });
    
  }


  export  function putMethod(end,data,header) {
    let options ={
      data : data[0],
      tail : end[1],
      id : data[1],
      headers : {
        'Content-Type': 'application/json',
        'Authorization' :  header
      } 
    }

   // console.log(data[1])
    return  axios.put(Network.network +'/' + end[0] ,options )
  }

 

