export function currentIndex(val){
    return{
        type : actionType.LAYOUT_CURRENT_INDEX,
        payload : val
    }
}

export function currentMenu(val){
   //console.log()
    return{
        type : actionType.LAYOUT_MENU,
        payload : val 
    }
}