import actionType from '../../constant/operator'

export function getOperator(){
    return{
        type : actionType.OPERATOR_LIST
    }
}

export function postOperator(data){
    return{
        type : actionType.OPERATOR_CREATE,
        payload : data
    }
}

export function deleteOperator(data){
    return{
        type : actionType.OPERATOR_DELETE,
        payload : data
    }
}

export function updateOperator(data1,data2){
    return{
        type : actionType.OPERATOR_UPDATE,
        payload : [data1,data2]
    }
}


