import React,{Component} from 'react';
import styles from './RadioButton.style' ;


class BillingRadio extends Component{

  constructor(props)
  {
    super(props)
  }
   
  render(){
    return(
        <div style={styles.container}>
             <input type="radio" name={this.props.radio1} value={this.props.value1} onChange={this.props.onchange} /><span style={styles.radio}> &nbsp;  {this.props.first} </span>
             
             <input type="radio" name={this.props.radio2} value={this.props.value2} style={{marginLeft: 60}} 
             defaultChecked={this.props.v2}  
             onChange={this.props.onchange} />
             <span style={styles.radio}>
                &nbsp;   {this.props.second} </span> 
             
        </div>
    )

  }

}

export default BillingRadio