import React , {Component} from  'react' ;
import styles from './Paintediv.style';

class PaintedDiv extends Component {
    constructor(props){
      super(props) 
      this.state = {

      }     
    }
    render(){
        return(
            <div style={{
                width : this.props.width,
                height : this.props.height ,
                backgroundColor : this.props.backgroundcolor,
                color : this.props.color,
               // textAlign : this.props.textalign,
                fontSize : this.props.fontsize ,
                fontFamily : this.props.fontfamily,
                letterSpacing : this.props.letterspacing,
                borderLeft : this.props.leftborder,
                borderRight : this.props.rightborder,
                borderTop : this.props.topborder,
                borderBottom : this.props.bottomborder,
               
            }}><div style={{ marginLeft : 10,display:'flex',flexDirection : 'row',textAlign : this.props.textalign}}> 
                <span style={{width : 100}}> {this.props.title} </span>
                { this.props.flag ? <span style={{width : 10 }}> :  </span> : null }  
                <span style={{}}> {this.props.text} </span>
             </div></div>
        )
    }
}

export default PaintedDiv ;