import actionType from '../../constant/billing'

const INITIAL_STATE ={
    isloading : true ,
    data : [], //record 
    basicInfo : {}
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.CUSTOMER_BILLING:
        return {
            isLoading : true,
        
        }

        case actionType.CUSTOMER_BILLING_SUCCESS :
            return {
               // ...state ,
                isloading : false ,
                data : action.data.billing_record_list,
              //  data : action.data.payable_billing_record_list,
                basicInfo : action.data.billing_account
            }

        
        default:
            return state;  
    }

}