import actionType from "../../constant/operator" ;
import Endpoint from '../../../utils/path/Endpoint';
import getMethod,{postMethod,deleteMethod,putMethod} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'


function* getOperator() {
     const token =  yield select(getToken) 
    try {
        const response = yield call(getMethod,Endpoint.getOperator,token)
        const data = yield response.data.payload;
        console.log("will be=="+data)
        yield put({ type:  actionType.OPERATOR_LIST_SUCCESS, data });
    } catch (e) {
       // yield put({ type:  actionType.HOME_GET_CURRENT_WETHER_FAILURE, e });
       // return;
       console.log(e)
    }    
}

function* postOperator(action) {
    const token =  yield select(getToken) 
    try {
        console.log("ok man ! post !!")
        const user  =action.payload ;
        console.log(user)
        const response = yield call(postMethod,Endpoint.postOperator,user,token)
        const data = yield response.data;
        const dataa = data ; //JSON.stringify(data.payload)
       // const dataStatus = data.httpStatus
        console.log("the response is ==== "+ dataa)
        if(JSON.stringify(data.status)=== '400')
        yield put({ type:  actionType.OPERATOR_CREATE_FAILURE });
        else 
        yield put({ type:  actionType.OPERATOR_CREATE_SUCCESS,dataa  });
    } catch (e) {
        yield put({ type:  actionType.OPERATOR_CREATE_FAILURE });
       // return;
       console.log(e)
    }    
}

function* deleteOperator(action) {
    const token =  yield select(getToken) 
    try {
        const h =action.payload ;
        console.log(h)
        const response = yield call(deleteMethod,Endpoint.deleteOperator,h,token);
        const data = yield response ;
        console.log("is=="+JSON.stringify(data))
        yield put({ type:  actionType.OPERATOR_DELETE_SUCCESS ,data });
    } catch (e) {
        console.log("error is = " )
        yield put({ type:  actionType.OPERATOR_DELETE_FAILURE, e });
        return;
       console.log(e)
    }

}

function* updateOperator(action) {
    const token =  yield select(getToken) 
    try {
        const h =action.payload ;
        console.log(h)
        const response = yield call(putMethod,Endpoint.updateOperator,h,token);
        const data = yield response ;
        console.log("is=="+JSON.stringify(data))
        yield put({ type:  actionType.OPERATOR_UPDATE_SUCCESS ,data });
    } catch (e) {
        console.log("error is = " )
        yield put({ type:  actionType.OPERATOR_UPDATE_FAILURE, e });
        return;
       console.log(e)
    }

}



export const operatorSaga = [
      takeEvery(actionType.OPERATOR_LIST, getOperator),
      takeEvery(actionType.OPERATOR_CREATE, postOperator),
      takeEvery(actionType.OPERATOR_DELETE,deleteOperator),
      takeEvery(actionType.OPERATOR_UPDATE,updateOperator)
];