import actionType from '../../constant/login'

const INITIAL_STATE ={
    isloading : true ,
    data : null  ,
    flag : 0 ,
    error : true  ,
    login : localStorage.getItem('login') ? localStorage.getItem('login') : false 
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.lOG_IN:
        return {
            ...state ,
            isLoading : true,
        
        }

        case actionType.lOG_IN_SUCCESS :
            return {
                 ...state ,
                error : false ,
                isloading : false ,
                data :action.data ,
                lognin : true ,
                storage : getLocalStoreData(action.data),
                flag : 0 
            }
            case actionType.lOG_IN_FAILURE :
            return {
                ...state ,
                error : true,
                flag : 1
            }

            case actionType.lOG_IN_MAKE_ERROR_TRUE :
            return {
                error : true
            }


        
        default:
            return state;  
    }

}


function getLocalStoreData(header) {

    localStorage.setItem('login','true')
    localStorage.setItem('header-z-my-point-new-look-my-base',header)
}