import React,{Component} from 'react';
import styles from './Registration.style' ;
import { Tabs,Tab,ProgressBar} from 'react-mdl';
import TextField from '../../components/textField/TextField';
import Label from '../../components/label/Label' ;
import BillButton from '../../components/button/Button' ;
import BillingRadio from '../../components/radio/RadioButton';
import BillDropDown from '../../components/dropdown/Dropdown' ;
import {connect} from 'react-redux' ;
import {createNewUser} from '../../redux/Action/registration';



class Registration extends Component{
    constructor(props){
        super(props)
        this.state = {
          activeTab : 0,
            newUser: {
              firstName: "",
              lastName: "",
              email: "",
              address: "",
              mobileNo: "",
              nidNumber: "",
              username: "",
              password: ""
                
              }
        }
    }

    componentDidMount(){
      const { match: { params } } = this.props;
      console.log("the customer === "+params.id)
    
  }

   input = (e,name) =>{
    let value = e.target.value;
    this.setState(
      prevState => ({
        newUser: {
          ...prevState.newUser,
          [name]: value 
        }
      })
    )
   }

   create = () =>{
    // console.log(this.state.newUser)
     const option = {
      "username": this.state.newUser.username,
      "password": this.state.newUser.password,
      "firstName": this.state.newUser.firstName,
      "lastName": this.state.newUser.lastName,
      "email": this.state.newUser.email,
      "address": this.state.newUser.address,
      "mobileNo": this.state.newUser.mobileNo,
      "nidNumber": this.state.newUser.nidNumber
    }
     this.props.createNewUser(option)
   }
      

  render(){
    console.log(this.props.data)
    return(
      <div>
         <div className="demo-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId)=>this.changeTab(tabId)} ripple>
                    {/* {this.props.msg} */}
                </Tabs>
               <span style={{marginLeft :20,color : 'red',letterSpacing:1.5}}>{this.props.msg}</span>
                <section style={{marginTop: 30}}>
                    <div style={this.state.activeTab === 0 ? {display : 'block'} : {display : 'none'}}>
                        <div style={styles.mainframe}>
                            <Label text ="First Name"/><TextField  label="First name "  onchange={(e)=>this.input(e,'firstName')}/>
                            <Label text="Last Name"/><TextField  label="Last name " onchange={(e)=>this.input(e,'lastName')} />
                            <Label text="Email"/><TextField  label="Email" onchange={(e)=>this.input(e,'email')}/>
                            <Label text="Address"/><TextField  label="Address"  onchange={(e)=>this.input(e,'address')} />
                            <Label text="Mobile "/><TextField  label="Mobile " pattern = "-?[0-9]*(\.[0-9]+)?" type="number" error ="not a mobile number !" 
                            onchange={(e)=>this.input(e,'mobileNo')}/>
                            <Label text="National ID"/><TextField  label="National ID " onchange={(e)=>this.input(e,'nidNumber')} />
                            <Label text="User Name"/><TextField  label="User name " onchange={(e)=>this.input(e,'username')} />
                            <Label text="Password"/><TextField  label="Password " type="password" onchange={(e)=>this.input(e,'password')} />
                            <BillButton text=" Submit  "  
                           // onclick={()=>this.changeTab(1)}
                            onclick={()=>this.create()}
                            width = {280}
                            />
                        </div>
                    </div>
                   
                </section>
            </div>  
      </div>
    )
  }
}

function mapStateToProps(state) {
	return {
        isLoading : state.regReducer.isLoading ,
        data : state.regReducer.data ,
        msg : state.regReducer.msg  
	};
}
function mapDispatchToProps(dispatch) {
	return {
		createNewUser : (val) =>{
            dispatch(createNewUser(val))
          }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Registration);