import { all } from 'redux-saga/effects';
import { homeSaga } from './home' ;
import { layoutSaga } from './layout' ; 
import {customerSaga} from './customer' ; 
import {regSaga} from './registration' ;
import {billingSaga} from './billing' ;
import {settingSaga} from './setting' ;
import {meterListSaga} from './meterlist' ;
import {loginSaga} from './login' ;
import {operatorSaga} from './operator'



export default function* rootSaga() {
    yield all([
        ...homeSaga ,
        ...layoutSaga ,
        ...customerSaga,
        ...regSaga,
        ...billingSaga,
        ...settingSaga ,
        ...meterListSaga ,
        ...loginSaga,
        ...operatorSaga
    ])
}