import React , {Component} from 'react' ;
import {Container,Row,Col,Button,Card} from 'react-bootstrap' ;
import styles from './NewAccount.style';
import { Tabs,Tab,ProgressBar} from 'react-mdl';
import TextField from '../../components/textField/TextField';
import Label from '../../components/label/Label' ;
import BillButton from '../../components/button/Button' ;
import BillingRadio from '../../components/radio/RadioButton';
import BillDropDown from '../../components/dropdown/Dropdown' ;
import InfoCard from '../../components/card/InfoCard' 

class createNewBillAccount extends Component {

    constructor(props){
        super(props)
        this.state = {
            id : '',
            cname : localStorage.getItem('name'),
            mobile : localStorage.getItem('mobile'),
            address : localStorage.getItem('address'),
            activeTab : 0,
            inputs : [],
            newAccount: {
            billAccountType : 'POSTPAID',
                billingCategory : '',
                meterDeviceId : '',
                regularBillingDay : '',
                lastBillingDate : '',
                lastBillingDateUnitSnapshot : '',
                usertext : '',
                password : '',
    
                timeoutDateTime : '',
                isEmergencyCreditEnabled : true ,
                maxEmergencyCredit : '',
                isReversePowerDeductionEnabled : true,
                isMaxPowerStatusEnabled : true ,
                maxPowerLevel : '',
                isAudibleAlarmEnabled : true,
                lowCreditAlarmValue : '',
                noOfTiers : 0,
                tierList : []
            }
        }
    }

    componentDidMount(){
        const { match: { params } } = this.props;
        console.log("the customer === "+params.id)
       this.setState({
           id : params.id
       })
    }

    changeTab = (tabId) =>{
        this.setState({
            activeTab: tabId
        })
    }
    handleInput(e,text) {
        let value = e.target.value;
       
        this.setState(
          prevState => ({
            newAccount: {
              ...prevState.newAccount,
              [text]: text ==='billingCategory' || text === 'regularBillingDay' ? parseInt(value) : value 
            }
          }),
          () => console.log(""+this.state.newAccount.billAccountType),
         // ()=>this.checkAllField()

        ) 
      }

    render(){
        return(
            <div>
                
                <Container style={{marginTop:10}}>
               
                <Row>
                    <Col xs={7}>
                    <Tabs activeTab={this.state.activeTab} onChange={(tabId)=>this.changeTab(tabId)} ripple style={styles.header}>
                    <Tab>Billing Info</Tab>
                    {
                  this.state.newAccount.billAccountType === 'PREPAID' ?  <Tab>Prepaid Info</Tab> : <Tab>Postpaid Info</Tab> 
                }   
                    <Tab>Notification</Tab>
                    </Tabs>
                    <section style={{marginTop: 30}}>
                    <div style={this.state.activeTab === 0 ? {display : 'block'} : {display : 'none'}}>
                        <div style={styles.mainframe}>
                        <div style={styles.innercontainer}>
                        <Label text="Billing Number " />
                        <TextField    />
                        </div>
                        <div style={styles.innercontainer}>
                        <Label text="Billing Account Address " />
                        <TextField    />
                        </div>
                        <div style={styles.innercontainer} >
                        <Label text="Billing Category"/>
                        <BillDropDown onchange={(e)=>this.handleInput(e,'billingCategory')} />
                        </div>
                        <div style={styles.innercontainer}>
                        <Label text="Miter Number on Billing Slip " />
                        <TextField    />
                        </div>
                        <div style={styles.innercontainer}>
                        <Label text="Billing Type"/>
                            <BillingRadio
                            radio1 = "radio" 
                            radio2 = "radio" 
                            v2="true" 
                            first="Prepaid"  second="Postpaid" 
                            value1="PREPAID" value2="POSTPAID" 
                            onchange={(e)=>this.handleInput(e,'billAccountType')} />
                        </div>
                        <div style={styles.innercontainer}>
                        <BillButton text=" Next "  
                           // onclick={ ()=>this.changeTab(this.state.newUser.billAccountType==='PREPAID' ?2 : 3 ) 
                            onclick={ ()=>this.changeTab(1)}
                            width = {300}
                            height = {35}
                            />
                        </div>

                        </div>
                     </div>
                     <div style={this.state.activeTab === 1 ? {display : 'block'} : {display : 'none'}}>
                     {
                      this.state.newAccount.billAccountType === 'POSTPAID' ?

                      <div  style={styles.mainframe}>
                            <Label text="Regular Billing Day"  />    
                            <TextField  label="Regular Billing Day " type="number" pattern="-?[0-9]*(\.[0-9]+)?" error ="Input not valid"  onchange={(e)=>this.handleInput(e,'regularBillingDay')}/>
                            <Label text="Billing Date"/><TextField type="date" label="" onchange={(e)=>this.handleInput(e,'lastBillingDate')}/>
                            <Label text="Regular Billing Payment Due Day"  />    
                            <TextField  label="Regular Billing Payment Date " type="number" pattern="-?[0-9]*(\.[0-9]+)?" error ="Input not valid"  onchange={(e)=>this.handleInput(e,'regularBillingPaymentDate')}/>
                            <Label text="Last Consume Unit"/><TextField  label="Last Consume Unit"   onchange={(e)=>this.handleInput(e,'lastBillingDateUnitSnapshot')}/>
                            
                            <BillButton text=" Next "  
                             onclick={ ()=>this.changeTab(2)}
                             width = {280}
                             height = {35}
                            />
                      </div> : 
                    <div style={styles.mainframe}>
                            <Label text="Timeout Date Time"  />
                            <TextField  label="Timeout Date Time"   onchange={(e)=>this.handleInput(e,'timeoutDateTime')}/>
                            <Label text="Emergency Credit Enabled" />
                            {/* may be manual radion button here */}
                            <BillingRadio
                            radio1 = "emcredit" 
                            radio2 = "emcredit" 
                            v2="true" 
                            first="no"  second="yes" 
                            value1={false} value2={true}
                            onchange={(e)=>this.handleInput(e,'isEmergencyCreditEnabled')} />

                            <Label text="Max Emergency Credit" />
                            <TextField  label="Max Emergency Credit" type="number" 
                            error = "Please insert a number " pattern="-?[0-9]*(\.[0-9]+)?"
                            onchange={(e)=>this.handleInput(e,'maxEmergencyCredit')}/>
                            
                            <Label text = "Reverse Power Deduction" /> 
                            <BillingRadio
                            radio1 = "repower" 
                            radio2 = "repower" 
                            v2="true" 
                            first="Disable"  second="Enable" 
                            value1="false" value2="true"
                            onchange={(e)=>this.handleInput(e,'isReversePowerDeductionEnabled')} />

                            <Label text="Max Power Status " />
                            <BillingRadio
                            radio1 = "maxpower" 
                            radio2 = "maxpower" 
                            v2="true" 
                            first="Disable"  second="Enable" 
                            value1="false" value2="true"
                            onchange={(e)=>this.handleInput(e,'isMaxPowerStatusEnabled')} />
                          
                           <Label text="Max Power Level" />
                            <TextField  label="Max Power Level" type="number" 
                            error = "Please insert a number " pattern="-?[0-9]*(\.[0-9]+)?"
                            onchange={(e)=>this.handleInput(e,'maxPowerLevel')}/>

                            <Label text="Audible Alarm"/>
                            <BillingRadio
                            radio1 = "aualarm" 
                            radio2 = "aualarm" 
                            v2="true" 
                            first="Disable"  second="Enable" 
                            value1="false" value2="true"
                            onchange={(e)=>this.handleInput(e,'isAudibleAlarmEnabled')} />

                            <Label text="Low Credit Alarm Value" />
                            <TextField  label="Low Credit Alarm Value" type="number" 
                            error = "Please insert a number " pattern="-?[0-9]*(\.[0-9]+)?"
                            onchange={(e)=>this.handleInput(e,'lowCreditAlarmValue')}/>

                            <Label text="No Of Tiers" />
                            <TextField  label="No Of Tiers" type="number" 
                            error = "Please insert a number " pattern="-?[0-9]*(\.[0-9]+)?"
                            onchange={(e)=>this.handleInput(e,'noOfTiers')}/>

                          { this.state.inputs.length > 0  ?  <Label text="Tier List" /> : null  }
                            {this.state.inputs.map(input => 
                              <TextField  label="Rate .." key={input} onchange={(e)=>this.handleInputTierList(e,input)} />
                              )}                            
                            
                            <BillButton text=" Next " 
                             onclick={()=>this.changeTab(2)}
                             width = {280}
                             height = {35}
                             />
                             <div style={{marginTop : 20}}></div>
                        </div>

                        }
                      </div>
                      <div style={this.state.activeTab === 2 ? {display : 'block'} : {display : 'none'}}>
                        <div style={styles.mainframe}>
                            {/* <Label text="User text"/>    <TextField  label="User text" onchange={(e)=>this.handleInput(e,'usertext')}/>
                            <Label text="Password"/>    <TextField type="password" label="Password" onchange={(e)=>this.handleInput(e,'password')}/> */}
                            <Label text="SMS Notification"/>
                            <BillingRadio
                            radio1 = "sms" 
                            radio2 = "sms" 
                            v2="true" 
                            first="No"  second="Yes" 
                            value1="no" value2="yes"
                            onchange={(e)=>this.handleInput(e,'isAudibleAlarmEnabled')} />
                            <Label text="Email Notification"/>
                            <BillingRadio
                            radio1 = "email" 
                            radio2 = "email" 
                            v2="true" 
                            first="No"  second="Yes" 
                            value1="no" value2="yes"
                            onchange={(e)=>this.handleInput(e,'isAudibleAlarmEnabled')} />
                            <BillButton text=" Submit " 
                            disabled = {this.state.disabledFlag} 
                            onclick={this.submit}
                            width = {220}
                             height = {35}
                            />
                            <div style={styles.successMsg}>
                            {
                              this.props.data === "200" && this.props.data !== 0  ? 
                              <span >Successfully new user created. <a onClick={this.riderectMe}>go to Customer page </a></span> : this.props.data === "500" ? <span > Failed to create  </span> :   null 
                            }
                            </div>
                        </div>
                       
                    </div>
                    </section>

                    
                    </Col>
                    <Col xs={1} style={{marginTop : 50}}></Col>
                    <Col xs={4} style={{marginTop : 50}}>
                        <InfoCard 
                         header = { "Customer : " + this.state.cname }
                         title = {"Mobile : " + this.state.mobile}
                         address = {"Address : " + this.state.address}
                        />
                    </Col>
                </Row>
                </Container>
            </div>

        )
    }
}

export default createNewBillAccount