import actionType from '../../constant/setting'

const INITIAL_STATE ={
    isloading : true ,
    data : []
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.SETTING_GET_VAT_VALUE:
        return {
            isLoading : true,
        
        }
        
        default:
            return state;  
    }

}