import actionType from '../../constant/customer'

const INITIAL_STATE ={
    isloading : true ,
    data : [],
    customer : []
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.CUSTOMER_LIST:
        return {
            isLoading : true,
        
        }

        case actionType.CUSTOMER_LIST_SUCCESS :
            return {
               // ...state ,
                isloading : false ,
                data : action.data[0]
            }

        case actionType.CUSTOMER_DATA : 
            return {
                ...state ,
                customer : action.payload
            }

        
        default:
            return state;  
    }

}