import React,{Component} from 'react';
import styles from './MeterList.style';
import {connect} from 'react-redux' ;
import CardMaster from '../../components/card/Card' ;
import {getMeterList} from '../../redux/Action/meterlist' ;
import {Grid,Cell} from 'react-mdl';
import InfoCard from '../../components/infocard/Infocard';
import {currentMenu} from  '../../../src/redux/Action/layout'

class MeterList extends  Component {

    constructor(props){
        super(props)
        this.state ={
            data : []
        }
    }

    componentDidMount(){
        const { match: { params } } = this.props;
        console.log("the customer === "+params.id)
        this.props.getMeterList(params.id)
    }

    static getDerivedStateFromProps(props,state){
        if(props.data !==null && props.data!== undefined ){
        return{
            data : props.data
        }
    }
    }

    billingDetails = (val,page) =>{
      //  alert(val)
      this.props.currentMenu(page)
        this.props.history.push('/billing/'+val);
    }
   


    render(){
            console.log("the data is =="+this.props.data)
        return(
            <div style={styles.container}>
            
                 <div style={{width: '100%'}} >
                 
                 <InfoCard 
                 id = {localStorage.getItem('customerid')}
                 name = {localStorage.getItem('name')}
                 mobileNo ={localStorage.getItem('mobile')}
                 address = {localStorage.getItem('address')}
                 />
                 <Grid className="demo-grid-ruler">
                {
                 this.state.data.map((data,index)=>
                    <div key={index}>
                    <Cell col={2}>
                    <CardMaster 
                        meterid = {data.meterDeviceId}
                        bid = {data.billAccountId}
                        type = {data.billingAccountType}
                        click = {()=>this.billingDetails(data.billAccountId,'Billing')}
                        />
                    </Cell>
                    </div> 
                  )
                }
                </Grid>    
                 </div>              
            </div>
        )
    }

}
function mapStateToProps(state) {
	return {
       // courseData:state
        // customer : state.customerReducer.customer,
        data : state.meterlistReducer.data
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        getMeterList : (val) =>{
             dispatch(getMeterList(val))
           },
           currentMenu : (val)=>{
               dispatch(currentMenu(val))
           }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(MeterList) ;
