import React , {Component} from 'react';
import {Grid,Cell} from 'react-mdl' ;
import styles from './Billing.style';
import PaintedDiv from '../paintediv/Paintediv'

class BillingComp extends Component {
    constructor(props){
        super(props)
        this.state ={
                name : '',
                address : ''
        }
    }

    componentDidMount(){
        this.setState({
            name : localStorage.getItem('name'),
            address : localStorage.getItem('address'), 
        })

    }

    static getDerivedPropsfromState(props,state){

    }

    render(){
        return(
            <div> 
               <div style={{width: '80%', margin: 'auto'}}>
               <PaintedDiv width={'100%'} height={40} backgroundcolor = {styles.hcolor}
               color = {styles.htcolor}
               textalign = "center" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.letter}
               flag = {false}
            //    title = "Billing"
               text="DHAKA  ELECTRIC POWER SUPPLY COMPANY  (DESCO)"
               />
                {/* name / address  */}
              <div style={{display:'flex',flexDirection:'row'}}>

              <div style={{width:'55%'}}>
              <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} leftborder = {styles.leftborder} rightborder={styles.rightborder} 
               flag = {true}
               title = "Name "
               text={ this.state.name}
               />
                <PaintedDiv width={'100%'} height={40} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} leftborder = {styles.leftborder}  rightborder={styles.rightborder} bottomborder = {styles.bottomborder}
               flag = {true}
               title = "Address "
               text={this.state.address}
               />

               </div>
                <div style={{width:'45%'}}>
               <PaintedDiv width={'100%'} height={20} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} leftborder = {styles.leftborder} rightborder={styles.rightborder} 
               flag = {true}
               title="Account No"
               text={this.props.account.substring(22)}
               />
               <PaintedDiv width={'100%'} height={20} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter}  rightborder={styles.rightborder} 
               flag = {true}
               title="Meter No"
               text={this.props.meter}
               />
               <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter}  rightborder={styles.rightborder} bottomborder = {styles.bottomborder}
               flag = {true}
               title = "Sanction Load"
               text={this.props.load}
               />
               </div>

              </div>

              {/*  next  */}

              <div style={{display:'flex',flexDirection:'row'}}>
                {/* left */}
              <div style={{width:'50%'}}>
              <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} leftborder = {styles.leftborder} rightborder={styles.rightborder}  bottomborder={styles.bottomborder}
               flag = {true}
               title = "Month"
               text={this.props.billmonth}
               />
               <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} leftborder = {styles.leftborder} rightborder={styles.rightborder}  bottomborder={styles.bottomborder}
               flag = {true}
               title = "Bill No"
               text={this.props.billid.substring(20)}
               />

              <div style={{display:'flex',flexDirection:'row'}}>
                <div style={{width:'50%'}}>
                <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} leftborder = {styles.leftborder} rightborder={styles.rightborder}  bottomborder={styles.bottomborder}
               flag = {true}
               title = "Issue Date"
               text={this.props.issue} 
               />
                </div>
                <div style={{width:'50%'}}>
                <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter}  rightborder={styles.rightborder}  bottomborder={styles.bottomborder}
               flag = {true}
               title = "Due Date"
               text={this.props.due} 
               />
                </div>
               </div>
               <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} leftborder = {styles.leftborder} rightborder={styles.rightborder}  bottomborder={styles.bottomborder}
               flag = {true}
               title ="Unit"
               text={this.props.consume}
               />
              </div>

              {/* right  */}
              <div style={{width:'50%'}}>
              <PaintedDiv width={'100%'} height={30} backgroundcolor = {styles.bcolor}
               color = {styles.btcolor}
               textalign = "left" fontsize = {styles.hfsize} fontfamily = {styles.fontfamily}
               letterspacing = {styles.bletter} rightborder={styles.rightborder}  bottomborder={styles.bottomborder}
               flag = {true}
               title = "Normal Charge"
               text={this.props.normalcharge}
               />
              </div>  
             </div>

             
               </div>
                 <h1></h1>
            </div>
        )
    }
}

export default BillingComp 