// A Meter Info Card 

import React , {Component}  from 'react';
import styles from './Card.style';
import {Card,CardTitle,CardText,CardActions,Button } from 'react-mdl';

class CardMaster extends Component {

    constructor(props){
        super(props)
        this.state = {
            
        }
    }


    render(){

        return(
            <div style={styles.container}>
                <Card shadow={6} style={styles.root}>
                    <CardTitle expand 
                     style={styles.cardTitle}
                    >
                    <div style={styles.meterTitle}>
                            <span style={styles.metertitle}>
                                Meter ID : </span>
                        <span style={styles.meterid}>   
                          {this.props.meterid} 
                            {/* {this.props.bid} */}
                            </span>
                           
                    </div>
                    </CardTitle>
                    <div style={styles.wrapText}>
                    <CardText style={styles.cardText} >
                        Amount : 24000
                        {/* {this.props.bid} */}
                    </CardText>
                    <CardText style={styles.cardText} >
                        Type : <span style={styles.textColor}> {this.props.type} </span>
                    </CardText>
                    </div>
                    <CardActions border style={styles.cardActions}>
                        <Button style={styles.buttonText} onClick={this.props.click}>View Ditails</Button>
                        {/* <span style={{backgroundColor : 'white'}}></span> */}
                    </CardActions>
                </Card>
                
            </div>
        )
    }

}

export default CardMaster ;