import actionType from "../../constant/login" ;
import Endpoint from '../../../utils/path/Endpoint';
import {postMethod,logpostMethod} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';

function* login(action) {
    try {
      const header = '';
        let val = action.payload 
       // console.log("the value is ==="+val.user)
        const response = yield call(postMethod,Endpoint.login,val,header)
         const d =  yield response.data;
         const dd = d.payload ;
         const data = dd[0].accessToken;
     //    console.log(data)
        //  const da = JSON.parse(d)
        //  console.log("the da is ===="+da)
        //  const dat = da['payload']
        //  const data = dat[0]['accessToken']
          yield put({ type:  actionType.lOG_IN_SUCCESS, data });  
        
    } catch (e) {
      //  yield put({ type:  actionType.lOG_IN_FAILURE, e });
       // return;
       console.log(e)
    }    
}

export const loginSaga = [
      takeEvery(actionType.lOG_IN, login)
];