import React, { Component } from 'react';
import { Layout, Header, Navigation, Drawer, Content,HeaderRow,Button } from 'react-mdl';
import { Link } from 'react-router-dom';
import Nav from '../navigation';
import styles from './Layout.style';
import {connect} from 'react-redux' ;
import {currentIndex,getTheApiLInk,currentMenu} from '../../redux/Action/layout' ;
import {makeErrorTrue} from '../../redux/Action/login'
import { withRouter } from 'react-router-dom'



class MeterTemplate extends Component {
    constructor(props) {
        super(props)
        this.state = { 
            currentmenu : 0,
            submenu : 100,
            display : 'none',
            data : [],
            called : false ,
            header  : {}
         };
    }

    componentDidMount(){
       // const val =  'Home' 
        // this.props.currentMenu(val)
    }

    // static getDerivedStateFromProps(props,state){
    //     if(props.header !==null && props.header!== undefined ){
    //     return{
    //         header : props.header 
    //     }
    // }
    // }

    makeCall = (val,name) =>{
        this.setState({
            currentmenu : val ,
    }) 
    
    this.props.currentMenu(name)
    }
 
    handleBack = () => {
        this.props.history.push('/reg')
      }

      signOut = () =>{
        this.props.currentMenu('Log IN')
        localStorage.clear()
        this.props.makeErrorTrue()

      }


  render() {

    return (
        <div style={styles.container}>       
        <Layout fixedHeader fixedDrawer>
        <Header title={<span><span ></span></span>}
         style={styles.header}
        > 
        
        <HeaderRow  > 
       
            
         <span style={styles.menu}>{this.props.menu}  </span>
         </HeaderRow>        
        </Header>
        <Drawer title={<span style={styles.title}>A-d-m-i-n</span>} style={{backgroundColor:'#f8f2bd'}} >
      
            {
                this.props.menu !== 'Login' ?
                <Navigation> 
                <Link to="/" style={this.state.currentmenu===0 ? styles.selected : styles.normal} onClick={()=>this.makeCall(0,'Home')}>All Customer</Link> 
                              
                {/* <Link to="/home" style={this.state.currentmenu===0 ? styles.selected : styles.normal} onClick={()=>this.makeCall(0,'Home')}>All Customer</Link>   */}
                <Link to="/reg/2" style={this.state.currentmenu===1 ? styles.selected : styles.normal} onClick={()=>this.makeCall(1,'Registration')}>Registration</Link>
                <Link to="/settings" style={this.state.currentmenu===2 ? styles.selected : styles.normal} onClick={()=>this.makeCall(2,'Setting')}>Settings</Link>
                <Link to="/operator" style={this.state.currentmenu===3 ? styles.selected : styles.normal} onClick={()=>this.makeCall(0,'OPERATOR MANAGEMENT')}>Operator Management</Link> 
                <span style={{ cursor:'pointer'}} onClick={this.signOut}>Sign out </span>
                </Navigation>
                : null 
            }
       
        
        
        </Drawer>
        <Content >
            {/* <h3>{this.props.header}  </h3> */}
            <Nav />
        </Content>
    </Layout> 
               
            
       
    </div>

    );
  }
}

function mapStateToProps(state) {
	return {
       // courseData:state
        isLoading : state.layoutReducer.isLoading ,
        data : state.layoutReducer.data,
        current : state.layoutReducer.current ,
        menu : state.layoutReducer.menu,
        back : state.layoutReducer.back,
        header : state.loginReducer.data ,//localStorage.getItem('header') ,
        error : state.loginReducer.error,
        login : state.loginReducer.login 
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
		// getMenuData : () =>{
        //     dispatch(getMenuData())
        //   },
        currentIndex : (val) =>{
            dispatch(currentIndex(val))
        },
        getTheApiLInk : () =>{
            dispatch(getTheApiLInk())
        },
        currentMenu : (val)=>{
            dispatch(currentMenu(val))
        },
        makeErrorTrue : () =>{
            dispatch(makeErrorTrue())
        }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(MeterTemplate);