import React,{Component} from 'react';
import styles from './Dropdown.style' ;
// import { RadioGroup,Radio } from 'react-mdl';


class BillDropDown extends Component{
   
  render(){
    return(
        <div style={styles.container}>
            <select style={styles.selectBtn} onChange={this.props.onchange}>
            <option value="">SELECT</option>
            <option value="1">LT-A</option>
            <option value="2">LT-B</option>
            <option value="3">LT-C</option>
            <option value="4">LT-D</option>
            </select>
        </div>
    )

  }

}

export default BillDropDown