import React , {Component} from 'react' ;
import {connect} from 'react-redux' ;
import {Table,Container,Row,Col,Form} from 'react-bootstrap';
import Label from '../../components/label/Label';
import Header from '../../components/alert/Alert' ;
import TextField from '../../components/textField/TextField';
import BillButton from '../../components/button/Button' ;
import {getOperator,postOperator,deleteOperator,updateOperator} from '../../../src/redux/Action/operator'
import styles from './Operators.style';
import { Gradient } from 'react-gradient';

class Operators extends Component {
    constructor(props){
        super(props)
        this.state = {
          gradient : [
               
            ['#F8F8F8','#f8f2bd']
            ],
          update : false ,
          text : "ADD NEW OPERATOR" ,
          deoId : null ,
          data : [],
          confirmpass : '',
          newUser: {
            firstName: "",
            lastName: "",
            deoEmployeeId : "",
            email: "",
            mobileNo: "",
            nidNumber: "",
            username: "",
            password: ""
              
            }  
        }
    }

    componentDidMount(){
      this.props.getOperator()
  }

  componentDidUpdate(prevProps, prevState) {

    const check = prevProps.message !== this.props.message  ? this.props.getOperator() : null
 
    }

  static getDerivedStateFromProps(props,state){
      if((props.data !== state.data )){
      return{
          data : props.data
      }      
  }
  return null;
}

    input = (e,name) =>{
      let value = e.target.value;
      this.setState(
        prevState => ({
          newUser: {
            ...prevState.newUser,
            [name]: value 
          }
        })
      )
    }

    confirmPass = (e) =>{
      let value = e.target.value;
     
      this.setState({
        confirmpass  : value 
    })
    }

    create = () =>{
    //  console.log(this.state.newUser)
      const option = {
      "deoEmployeeId" : this.state.newUser.deoEmployeeId,
       "username": this.state.newUser.username,
       "password": this.state.newUser.password,
       "firstName": this.state.newUser.firstName,
       "lastName": this.state.newUser.lastName,
       "email": this.state.newUser.email,
       "mobileNo": this.state.newUser.mobileNo,
       "nidNumber": this.state.newUser.nidNumber
     } 
   //  console.log(option)
      this.props.postOperator(option)
      this.makeInitial()
      
    }

    makeInitial = () =>{
      this.setState({
        update : false ,
        newUser: {
          firstName: "",
          lastName: "",
          deoEmployeeId : "",
          email: "",
          mobileNo: "",
          nidNumber: "",
          username: "",
          password: ""
            
          }
      })
    }

    changeEnv = (data) =>{
    //  console.log(data)
      this.setState({
        update : true ,
        text : "Operator Details",
        deoId : data.deoId,
        newUser: {
          ['deoEmployeeId'] : data.deoEmployeeId,
          ['firstName'] : data.firstName,
          ['lastName'] : data.lastName,
          ['email'] : data.email,
          ['mobileNo'] : data.mobileNo,
          ['nidNumber'] : data.nidNumber,
          ['username'] : data.username 
        }
        
      })
    }

    update = () =>{
      const deoid = this.state.deoId
      const option = {
        "deoId" : this.state.deoId ,
        "deoEmployeeId" : this.state.newUser.deoEmployeeId,
         "username": this.state.newUser.username,
         "firstName": this.state.newUser.firstName,
         "lastName": this.state.newUser.lastName,
         "email": this.state.newUser.email,
         "mobileNo": this.state.newUser.mobileNo,
         "nidNumber": this.state.newUser.nidNumber
       } 
     //  console.log(option)
        this.props.updateOperator(option,deoid)
        this.makeInitial()
    }

    delete = () =>{
    const id = this.state.deoId 
    this.props.deleteOperator(id)
    this.makeInitial()
    }

    cancel = ()=>{
      this.setState({
        update : false ,
        text : "ADD NEW OPERATOR" ,
        newUser: {
          firstName: "",
          lastName: "",
          deoEmployeeId : "",
          email: "",
          mobileNo: "",
          nidNumber: "",
          username: "",
          password: ""
            
          } 
      })
      
    }

   
    render(){
    //  console.log(this.state.data)
        return(
            <div>
               <Gradient
                gradients={ this.state.gradient } // required
                property="background"
                duration={ 10000 }
                angle="120deg"
            > 

                <div style={{margin:20}}>
                <Container>
                <Row >
                    <Col  md={8}>
                <Table  bordered hover  style={{borderRadius : 10}}>
                  <thead>
                    <tr style={{backgroundColor:'#1c9cea',color:'white'}}>
                      <th>#</th>
                      <th>ID</th>
                      <th>Name</th>
                      <th>Email</th>
                      <th>Mobile</th>
                    </tr>
                  </thead>
                  <tbody style={styles.tbody}>
                    {
                      this.state.data !== undefined ? 
                      this.state.data.map((data,index)=>
                      <tr key={index} style={{cursor: 'cell'}} onClick={()=>this.changeEnv(data)}>
                       <td>{index+1}</td>
                      <td>{data.deoEmployeeId}</td>
                      <td>{data.firstName}</td>
                      <td>{data.email}</td>
                      <td>{data.mobileNo}</td>
                    </tr>
                      )  : null
                    }
                    
                   
                  </tbody>
                </Table>
                </Col>

                <Col md={4}  style={{backgroundColor:'#ffffff'}}>
                
              <Header  variant="info"  text = {this.state.text} width = '90%' />
              <Form style={{backgroundColor:'transparent'}}>
                <Form.Group >
                <Label text="Employee ID "/>
                <TextField  placeholder = ""
                value = { this.state.newUser.deoEmployeeId }
              
                onchange={(e)=>this.input(e,'deoEmployeeId')}/>
                </Form.Group>

                <Form.Group >
                <Label text="First Name "/>
                <TextField  placeholder = "" 
                value = {  this.state.newUser.firstName }
                onchange={(e)=>this.input(e,'firstName')}/>
                </Form.Group>

                <Form.Group >
                <Label text="Last Name "/>
                <TextField  placeholder = ""
                 value = {  this.state.newUser.lastName  }
                onchange={(e)=>this.input(e,'lastName')}/>
                </Form.Group>

                <Form.Group >
                <Label text="Email Name "/>
                <TextField  placeholder = "" 
                 value = { this.state.newUser.email  }
                onchange={(e)=>this.input(e,'email')} />
                </Form.Group>

                <Form.Group >
                <Label text=" Mobile No  "/>
                <TextField  placeholder = "" 
                value = { this.state.newUser.mobileNo  }
                pattern = "-?[0-9]*(\.[0-9]+)?" type="number" error ="not a mobile number !" 
                            onchange={(e)=>this.input(e,'mobileNo')}/>
                </Form.Group>

                <Form.Group >
                <Label text="NID Number "/>
                <TextField  placeholder = ""
                value = {  this.state.newUser.nidNumber  }
                onchange={(e)=>this.input(e,'nidNumber')} />
                </Form.Group>

                <Form.Group >
                <Label text="UserName "/>
                <TextField  placeholder = "" 
                value = {  this.state.newUser.username }
                onchange={(e)=>this.input(e,'username')}/>
                </Form.Group>

                <Form.Group >
                <Label text="Password "/>
                <TextField  placeholder = "******" type="password" onchange={(e)=>this.input(e,'password')}/>
                </Form.Group>

                <Form.Group >
                <Label text="Confirm Pasword "/>
                <TextField  placeholder = "******"  type="password" onchange={(e)=>this.confirmPass(e)} />
                </Form.Group>


                <Form.Group >
                  {
                    !this.state.update ? 
                    <BillButton 
                    text = "Submit "
                    width = '90%'
                    onclick={()=>this.create()}
                    /> :
                    <div style={styles.updateBtn}>
                    <BillButton
                    variant =  "danger" 
                    text = "Update !"
                    width = '20vh'
                    onclick={()=>this.update()}
                    />
                    <BillButton 
                    variant =  "success" 
                    text = "Cancel"
                    width = {80}
                    onclick={()=>this.cancel()}
                    />

                    <BillButton 
                    variant =  "link" 
                    text = "Delete"
                    width = {50}
                    onclick={()=>this.delete()}
                    />
                    </div>
                  }
              
                </Form.Group>

                </Form>
                </Col>
                </Row>
               
                </Container>
               
</div>
</Gradient>
            </div>
        )
    }
}


function mapStateToProps(state) {
	return {
       
        data : state.operatorReducer.data,
        message : state.operatorReducer.message
       
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
		getOperator : () =>{
            dispatch(getOperator())
          },
    postOperator : (val) =>{
            dispatch(postOperator(val))
    },
    deleteOperator : (val) => {
            dispatch(deleteOperator(val))
    },
    updateOperator : (val1,val2) =>{
            dispatch(updateOperator(val1,val2))
    }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Operators) ;