import actionType from "../../constant/customer" ;
import Endpoint from '../../../utils/path/Endpoint';
import getMethod,{getMethodById} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'


function* getCustomer() {
     const token =  yield select(getToken) 
   // console.log("the token is ================="+(token))
    try {
        const response = yield call(getMethod,Endpoint.getCustomer,token)
        //const data = yield response.data.payload;
        const data = yield response.data.payload;
      //  console.log("will be=="+data)
        yield put({ type:  actionType.CUSTOMER_LIST_SUCCESS, data });
    } catch (e) {
       // yield put({ type:  actionType.HOME_GET_CURRENT_WETHER_FAILURE, e });
       // return;
       console.log(e)
    }    
}



export const customerSaga = [
      takeEvery(actionType.CUSTOMER_LIST, getCustomer)
];