import actionType from "../../constant/registration" ;
import Endpoint from '../../../utils/path/Endpoint';
import {postMethod} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'

function* createNewUser(action) {
    const token =  yield select(getToken) 
    try {
        const user  =action.payload ;
        const response = yield call(postMethod,Endpoint.createCustomer,user,token)
        const data = yield response.data;
        const dataa = JSON.stringify(data.payload)
       // const dataStatus = data.httpStatus
        console.log("the response is ==== "+ JSON.stringify(data.status))
        if(JSON.stringify(data.status)=== '400')
        yield put({ type:  actionType.REGISTARTION_CREATE_FAILURE });
        else 
        yield put({ type:  actionType.REGISTARTION_CREATE_SUCCESS,dataa  });
    } catch (e) {
        yield put({ type:  actionType.REGISTARTION_CREATE_FAILURE });
       // return;
       console.log(e)
    }    
}

export const regSaga = [
      takeEvery(actionType.REGISTARTION_CREATE, createNewUser)
];