import actionType from '../../constant/registration';


const INITIAL_STATE = {
    
    isLoading : false ,
    data : 0,
    error : false ,
    msg : "Please fill all the field  ."
}

export default (state=INITIAL_STATE,action)=>{

    switch(action.type){

        case  actionType.REGISTARTION_CREATE  : 
        return {
            isLoading : true
        }

        case actionType.REGISTARTION_CREATE_SUCCESS : 
        return {
            isLoading : false ,
            data : action.dataa,
            error : false ,
            msg : "Successfully User Created."
        }
        case actionType.REGISTARTION_CREATE_FAILURE : 
        return {
            isLoading : true ,
            error : true,
            msg : "Something went wrong .Please fill all the field  ."
        }

         default : 
        return state 
    }
}