import actionType from "../../constant/home" ;

import { takeEvery, call, put, select } from 'redux-saga/effects';

function* getMenuData() {
    console.log("sagas")
    try {
        console.log("try")
        const response = yield call(fetch, 'http://192.168.106.189:8080/Hierarchy_Management/HierarchyManagerRetrieveList');
        const data = yield response.json();
       // console.log(data)
        yield put({ type:  actionType.HOME_GET_MENU_DATA_SUCCESS, data });
    } catch (e) {
       // yield put({ type:  actionType.HOME_GET_CURRENT_WETHER_FAILURE, e });
       // return;
       console.log(e)
    }

    

}

export const homeSaga = [
      takeEvery(actionType.HOME_GET_MENU_DATA, getMenuData)
    

];