import React , {Component} from 'react' ;
import {Container,Row,Col,Card,Button,Form} from 'react-bootstrap';
import Label from '../../components/label/Label'
import TextField from '../../components/textField/TextField';
import BillButton from '../../components/button/Button'
import styles from './Login.style';
//import Desco from '../../components/desco/Desco';
import {connect} from 'react-redux' ;
import { Gradient } from 'react-gradient';
import {login} from '../../redux/Action/login'
import { Route , withRouter} from 'react-router-dom';
import {currentMenu} from '../../redux/Action/layout' ;
import Customer from '../customer/Customer'



class Login extends Component {
    constructor(props){
        super(props)
        this.state = {
            username : '',
            password : '',
            gradient : [
               //['#a23628','#007bff'],
             //  ['#007bff','#a23628']
            //  ['#a28628','#f8f2bd'],
           // ['#fffff9','#000000'],
           ['#585858','#f8f2bd']
            ]
        }
    }

    componentDidMount(){
        this.props.currentMenu('Login')
   }

    username = (e) =>{
        let value = e.target.value ;
        this.setState({
            username : value 
        })
    }

    password = (e) =>{
        let value = e.target.value ;
        this.setState({
            password : value 
        })
    }

    makeLogin = () =>{
      //  let history = useHistory()
        let user = this.state.username ;
        let pass = this.state.password ;

        let option = {
            username : user ,
            password : pass
        }

         //if(user==='' && pass==='')
        this.props.login(option)
        if(!this.props.error)
        this.props.currentMenu('Home')

    }



    render(){
        // const result = this.props.data !== null  || this.props.data !== undefined ?  Object.values(this.props.data)
        // : null 
        // console.log("data is=="+this.props.data[0])
        return(
            <div>
            <Gradient
                gradients={ this.state.gradient } // required
                property="background"
                duration={ 10000 }
                angle="120deg"
            >
            <div  style={styles.container}>
            <div >
                    <Card style={styles.card}>
                    {
                        this.props.flag === 1   ? 
                        <span style={styles.txtMsg}> Wrong user name or password </span> : null 
                    }
                    
                    
                     <Card.Body>
                    <Card.Img className="text-center" src={require('../../assets/images/desco.png')} style={{width :100,margin : 10,marginTop : 0}}/> 
                   
                    <Label text="User Name" color = "white" />

                    <TextField placeholder ="User ID" onchange={(val)=>this.username(val)} />

                    <Label text="Password" color = "white" />
                    <TextField placeholder ="*******" type="password" onchange={(val)=>this.password(val)} />
                          
                    <BillButton variant="info"
                            color = "white" width = {280}
                            text = "LOG IN !" onclick={this.makeLogin} />   
                     
                    </Card.Body>
                    </Card>
                   
            </div>
            </div>
            </Gradient>
            </div>
                
        )
        
    }
}


function mapStateToProps(state) {
	return {

       data  : state.loginReducer.data,
       error : state.loginReducer.error,
       flag : state.loginReducer.flag 
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        login : (val) =>{
             dispatch(login(val))
           },

        currentMenu : (val)=>{
            dispatch(currentMenu(val))
        }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Login)