import actionType from '../../constant/home'

const INITIAL_STATE ={
    isloading : true ,
    data : []
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.HOME_GET_MENU_DATA:
        return {
            isLoading : true,
        
        }

        case actionType.HOME_GET_MENU_DATA_SUCCESS :
            return {
               isloading : false ,
               data : action.data.RETURNS.APIPAYLOAD
            }
        
        default:
            return state;  
    }

}