// export default Label;

import {label } from 'react-bootstrap';
import styles from './Label.style'

class Label extends Component {

    render(){
        return(
            <div>
                <label htmlFor="basic-url"
                 style={{...styles.label,color : this.props.color}}>{this.props.text}</label>
            </div>
        )
    }
}

export default Label 