import React,{Component} from 'react';
import styles from './Button.style' ;
//import { Button } from 'react-mdl';
import Button from 'react-bootstrap/Button';

class BillButton extends Component {

    render(){
        return(
            <div>
            {/* <Button raised ripple primary  style={styles.button} variant={this.props.variant}
            onClick={this.props.onclick} error ={this.props.error} disabled={this.props.disabled}>
                {this.props.text}
            </Button> */}

            <Button variant={this.props.variant} style={{...styles.button,width : this.props.width,
                height : this.props.height,color:this.props.color}} block 
                onClick={this.props.onclick}>{this.props.text}</Button>
            </div>
        )
    }

}

export default BillButton