import actionType from "../../constant/meterlist" ;
import Endpoint from '../../../utils/path/Endpoint';
import {getMethodById} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';

function* getMeterList(action) {
    try {
        let val = action.payload 
     //   console.log("the value is ==="+val)
        const response = yield call(getMethodById,Endpoint.customerBillMeterList,val)
        const data = yield response.data;
        console.log("the return is =="+data)
        yield put({ type:  actionType.METER_LIST_SUCCESS, data });
    } catch (e) {
       // yield put({ type:  actionType.HOME_GET_CURRENT_WETHER_FAILURE, e });
       // return;
       console.log(e)
    }    
}

export const meterListSaga = [
      takeEvery(actionType.METER_LIST, getMeterList)
];