import React, { Component } from 'react';
import {connect} from 'react-redux' ;
import { Switch, Route } from 'react-router-dom';
import Login from '../../src/pages/login/Login' ;
import Registration from '../../src/pages/reg/Registration' ;
import Customer from '../../src/pages/customer/Customer' ;
import Billing from '../../src/pages/billing/Billing' ;
import Setting from '../../src/pages/setting/Setting' ;
import MeterList from '../../src/pages/meterList/MeterList';
import createNewBillAccount from '../../src/pages/newAccount/NewAccount';
import Operators from '../../src/pages/operator/Operators'

class nav extends Component{
    render(){
      console.log(this.props.login)
      return (
       
        <Switch >
        {
          this.props.error && !this.props.login ? 
         <Switch>
          <Route exact  path = "/"  component={Login}  />
          <Route path = "/meterList/:id"  component={Login}  />
          <Route  path="/reg" component={Login} style={{backgroundCOlor:'white'}} />
          <Route path ="/billing/:id" component={Login} />
          <Route path = "/settings"  component={Login}  />
          <Route path = "/operator"  component={Login}  />
         </Switch>
          :
         <Switch>
          <Route exact path="/"   component={Customer} />
          <Route path = "/meterlist/:id"  component={MeterList}  />
          <Route path = "/createnew/:id"  component={createNewBillAccount}  />
          {/* <Route  path="/reg" component={Registration} style={{backgroundCOlor:'white'}} /> */}
          <Route  path="/reg/:id" component={Registration} style={{backgroundCOlor:'white'}} />
          <Route path ="/billing/:id" component={Billing} />
          <Route path = "/settings"  component={Setting}  />
          <Route path = "/operator"  component={Operators}  />
          </Switch>
        }
          
         
         
          

        </Switch>
      )
    }
}
function mapStateToProps(state) {
	return {

       data  : state.loginReducer.data,
       error : state.loginReducer.error,
       login : localStorage.getItem('login') ? localStorage.getItem('login') : state.loginReducer.login
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        // login : (val) =>{
        //      dispatch(login(val))
        //    }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(nav);