import React , {Component}  from 'react';
//import styles from './Infocard.style';
import styles from '../../pages/customer/Customer.style'
import {Grid,Cell} from 'react-mdl';

class InfoCard extends Component{
    
    render(){
        return(
            <div style={{width: '95%', margin: 'auto'}}>
                   <Grid className="demo-grid-ruler" style={styles.tableHeader}  shadow={1} noSpacing>
                    <Cell col={4} style={styles.headerCell}>Customer ID </Cell>
                    <Cell col={2} style={styles.headerCell}>Name</Cell>
                    <Cell col={2} style={styles.headerCell}>Mobile No</Cell>
                    <Cell col={4} style={styles.headerCell}>Address</Cell>
                    
                </Grid>
                <Grid className="demo-grid-ruler" style={{backgroundColor:'#ecc0c0',color:'black',borderRadius:5}} noSpacing>
                    <Cell col={4} style={styles.headerCell}> {this.props.id}</Cell>
                    <Cell col={2} style={styles.headerCell}>  {this.props.name}</Cell>
                    <Cell col={2} style={styles.headerCell}>  {this.props.mobileNo} </Cell>
                    <Cell col={4} style={styles.headerCell}>  {this.props.address} </Cell>
                </Grid>
            </div>
        )
    }
}

export default InfoCard