import React , {Component} from 'react';
import { Grid,Cell } from 'react-mdl';
import styles from './Setting.style';
import {connect} from 'react-redux' ;
import {getCustomerBilling} from '../../redux/Action/billing';
import { Tabs,Tab} from 'react-mdl';
import Label from '../../components/label/Label' ;
import TextField from '../../components/textField/TextField';
import BillButton from '../../components/button/Button' ;

class Setting extends Component{
    constructor(props){
        super(props)
        this.state = {
             activeTab: 0
        }

    }

    componentDidMount(){
       
    }
    handleInput = (e) =>{
        console.log(e.target.value)
    }
    
    render(){
        console.log(this.props.data)
        return(
            <div className="demo-tabs">
            <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple style={styles.header}>
                <Tab>Vat</Tab>
                <Tab>Demand Charge</Tab>
                <Tab>Payment </Tab>
                <Tab>Other</Tab>
                <Tab>Other</Tab>
                <Tab>Other </Tab>
                
            </Tabs>
            <section style={{marginTop: 30}}>
                <div style={styles.mainframe}>
                <div style={this.state.activeTab === 0 ? {display : 'block'} : {display : 'none'}}>
                            
                        <Label text="Vat Amount(%)"/>
                        <TextField  label="First name "  onchange={(e)=>this.handleInput(e)}/>
                        <BillButton text=" Update " 
                        width = {280}
                        />
                        
                </div>

                <div style={this.state.activeTab === 1 ? {display : 'block'} : {display : 'none'}}>
                            
                            <Label text="Demand Charge"/>
                            <TextField  label="First name "  onchange={(e)=>this.handleInput(e)}/>
                            <BillButton text=" Update " 
                            width = {280}
                            />
                            
                    </div>
                <div style={this.state.activeTab === 2 ? {display : 'block'} : {display : 'none'}}>
                            
                            <Label text="Late Fee"/>
                            <TextField  label="First name "  onchange={(e)=>this.handleInput(e)}/>
                            <BillButton text=" Update " 
                            width = {280}
                            />
                            
                    </div>
                </div>
            </section>
        </div> 
        )
    }
}

function mapStateToProps(state) {
	return {
       // courseData:state
        customer : state.customerReducer.customer,
        data : state.billingReducer.data
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        getCustomerBilling : (val) =>{
             dispatch(getCustomerBilling(val))
           }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Setting);