import React , {Component,Suspense} from 'react';
import {connect} from 'react-redux' ;
import {getCustomer,customerData} from '../../../src/redux/Action/customer'
import {currentMenu} from  '../../../src/redux/Action/layout'
import {Grid,Cell,Tooltip,Icon } from 'react-mdl';
import styles from './Customer.style';
import ShowImage from '../../components/image/Image';
import { Gradient } from 'react-gradient';

//import SearchBox from '../../components/search'
const  SearchBox = React.lazy(() => import('../../components/search'))
//const ExampleComponent = React.lazy(() => import('./ExampleComponent'));  

class Customer extends Component{
    constructor(props){
        super(props)
        this.state = {
            gradient : [
               
             ['#F8F8F8','#f8f2bd']
             ],
            flag : true ,
            data : [
                {

                    customerId : '999',
                firstName : 'James',
                mobileNo : 32,
            },
            {
                
                customerId : '991',
                firstName : 'James',
                mobileNo : 31,
            },
                        ]
                    }

      
    }

    componentDidMount(){
        this.props.getCustomer() 
        this.props.currentMenu('Home')
    }

    static getDerivedStateFromProps(props,state){
     //alert(state.flag )
        if(props.data!==null && props.data!==undefined && state.flag  ){
        return{
            data : props.data
        }
    }

    return null 

    }
    
    goBilling = (id,data,name) =>{
        //alert(id)
      let   option = {
            "id" : data.customerId,
            "name" : data.firstName,
            "lastname" : data.lastName === null ? ' ' : data.lastName,
            "mobile" : data.mobileNo,
            "address" : data.address
        } 

        localStorage.setItem('name',option.name + ' ' +  option.lastname)
        localStorage.setItem('address', option.address)
        localStorage.setItem('mobile', option.mobile)
        localStorage.setItem('customerid', option.id)
        let calls = name === 'Meter-List' ? this.props.customerData(option) : null // here go supporting API's call
        
         this.props.currentMenu(name)
        let page = name === 'Meter-List' ? this.props.history.push('/meterlist/'+id) : 
                                           this.props.history.push('/createnew/'+id)
    }

    searchNow = (value ) =>{
        //alert(this)
        // this.setState({
        //     data : this.state.data.filter(data =>  data.firstName.includes(value) || data.lastName.includes(value)
        //     || data.mobileNo.includes(value) || data.customerId.includes(value))
        // })

        console.log(value)

        this.setState({
            data : this.state.data.filter(data =>  data.firstName.includes(value) || data.mobileNo.includes(value) )
        })
    }

    searchText = (val) =>{
       // alert('blavkk')
         let value = val.target.value ;
        this.setState({
            flag : value !== '' ?  false  : true 
        },(e)=>this.searchNow(value))      
       
    }

    updateFor = () => {
        this.props.currentMenu('Registration Update : ')
        const id = 1 
        this.props.history.push('/reg/'+id)
    }

    render(){
       // console.log(this.props.header)
       // console.log(this.state.data)
        return(
            <div>
                 <Gradient
                gradients={ this.state.gradient } // required
                property="background"
                duration={ 10000 }
                angle="120deg"
            > 
                <div style={styles.container}>
                    <div style={styles.searchBox}>
                    <Suspense fallback={<div>Loading...</div>}>  
                    <SearchBox 
                    label = "search customer.."
                    
                    onchange = {(val)=>this.searchText(val)}
                    />
                    </Suspense>
                </div>
                {/* <input  onChange = {(val)=>this.searchText(val)} /> */}
                <div style={{width: '80%', margin: 'auto'}}>
                <Grid className="demo-grid-ruler" style={styles.tableHeader}  shadow={0} noSpacing>
                    <Cell col={1} style={styles.headerCell}>#</Cell>
                    <Cell col={5} style={styles.headerCell}>Customer ID </Cell>
                    <Cell col={2} style={styles.headerCell}>Name</Cell>
                    <Cell col={2} style={styles.headerCell}>Mobile No</Cell>
                    
                </Grid>
                {
                this.state.data.map((data,index)=>
                <Grid className="demo-grid-ruler" style={index%2===0 ? styles.tableBody : styles.tableBodyEven}  shadow={2} key={index} noSpacing>
                    <Cell col={1} style={styles.headerCell} onClick={this.updateFor}> {index+1}</Cell>
                    <Cell col={5} style={styles.headerCell} onClick={this.updateFor}> {data.customerId}</Cell>
                    <Cell col={2} style={styles.headerCell} onClick={this.updateFor}>{data.firstName} </Cell>
                    <Cell col={2} style={styles.headerCell} onClick={this.updateFor}>  {data.mobileNo} </Cell>
                    <Cell col={1} style={styles.headerCell}> 
                    <Tooltip label=" All Billing Account" position="right">
                    <a style={{cursor: 'pointer'}} 
                    onClick={(id)=>this.goBilling(data.customerId,data,'Meter-List')}>
                    <ShowImage src="https://img.icons8.com/flat_round/64/000000/arrow-right.png" width = {20} />
                    </a>
                    </Tooltip>
                    </Cell>

                    <Cell col={1} style={styles.headerCell}> 
                    <Tooltip label="Create New Account" position="right">
                    <a style={{cursor: 'pointer',width : 130}} 
                    onClick={(id)=>this.goBilling(data.customerId,data,'Create New Account')}> 
                    <ShowImage  src="https://img.icons8.com/plasticine/100/000000/add.png" width = {25}  />
                    </a>
                    </Tooltip>
                    </Cell>
                </Grid>
                )
                 }
            </div>
            </div>    
            </Gradient>
            </div>
        )
    }
}

function mapStateToProps(state) {
	return {
       // courseData:state
        isLoading : state.customerReducer.isLoading ,
        data : state.customerReducer.data,
        header :  localStorage.getItem('login') ? localStorage.getItem('header') : state.loginReducer.data
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
		getCustomer : () =>{
            dispatch(getCustomer())
          },

        customerData : (val) =>{
            dispatch(customerData(val))
        },
        currentMenu : (val)=>{
            dispatch(currentMenu(val))
        }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Customer);