import React , {Component} from 'react';
import { Grid,Cell } from 'react-mdl';
import styles from './Billing.style';
import {connect} from 'react-redux' ;
import {getCustomerBilling} from '../../redux/Action/billing';
import BillingComp from '../../components/billing/Billing';
import Label from '../../components/label/Label';
import {currentMenu} from  '../../../src/redux/Action/layout'

class Billing extends Component{
    constructor(props){
        super(props)
        this.state = {
            data : [],
            info : {}
        }
      
    }

    componentDidMount(){
        const { match: { params } } = this.props;
        console.log("the customer === "+params.id)
        this.props.getCustomerBilling(params.id)
    }
    static getDerivedStateFromProps(props,state){
        if(props.data !==null && props.data!== undefined ){
        return{
            data : props.data,
            info : props.info 
        }
    }
    }

    convertDate = (d) =>{
        var  date = new Date(d)
        var dd = date.getDate(); 
        var mm = date.getMonth()+1;
        var yyyy = date.getFullYear(); 
        if(dd<10){dd='0'+dd} 
        if(mm<10){mm='0'+mm};
        return d = dd+'/'+mm+'/'+yyyy
    }

    getMonth = (d) =>{
        const monthNames = ["January", "February", "March", "April", "May", "June",
         "July", "August", "September", "October", "November", "December"
];

        var  date = new Date(d)
        var mm = date.getMonth() ;
        var yyyy = date.getFullYear(); 
        // const d = new Date(d);
       return  monthNames[mm] +','+ yyyy 
    }
    handleBack = () => {
        let page = 'Meter List' ;
        this.props.currentMenu(page)
        this.props.history.goBack()
      }
    
    render(){
        console.log(this.state.data)
        return(
            <div>
                <Label  onclick={()=>this.handleBack()} name="<< back" />
            {  
                this.state.data.length > 0 ? 
                this.state.data.map((data,index)=>
                <div style={{margin:5}} key={index}>
                <BillingComp 
                account = {this.state.info.billAccountId}
                meter = {this.state.info.meterDeviceId}
                load = "2.00"
               // billmonth = {new Date(data.currentBillingTimeStamp).toLocaleString()}
             //  billmonth = {this.convertDate(data.currentBillingTimeStamp)}
                billmonth = {this.getMonth(data.currentBillingTimeStamp)}
                billid = {data.billingRecordId}
                issue = {this.convertDate(data.currentBillingTimeStamp)}
                due = {this.convertDate(data.currentBillingTimeStamp)}
                consume = {data.consumedUnitsForThisBillingCalculationWindow}
                normalcharge = {data.currentBillingTimeStampUnitValue}
               />
               <div style={{marginTop:10}}>  </div>
            </div>
                )

                : <span> no record found  </span>
            }
               
            </div>
        )
    }
}

function mapStateToProps(state) {
	return {
       // courseData:state
        customer : state.customerReducer.customer,
        data : state.billingReducer.data,
        info : state.billingReducer.basicInfo
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        getCustomerBilling : (val) =>{
             dispatch(getCustomerBilling(val))
           },
           currentMenu : (val)=>{
               dispatch(currentMenu(val))
           }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Billing);