import actionType from '../../constant/meterlist';

const  INITIAL_STATE = {
    isLoading : true ,
    data  : []
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.METER_LIST:
        return {
            isLoading : true,
        
        }

        case actionType.METER_LIST_SUCCESS :
            return {
               // ...state ,
                isloading : false ,
                data : action.data.billing_account_list
            }

        
        default:
            return state;  
    }

}